import React from "react";

export default function CardItem({ carditem, up, down, del }) {
  return (
    <div
      className={
        carditem.quantity * carditem.price > 20000
          ? "card-item more-than-twenty"
          : "card-item"
      }
    >
      <p className="title">{carditem.name}</p>
      <p>quantity-{carditem.quantity}</p>
      <p className="subtotal">
        subtotal-{carditem.quantity * carditem.price}AMD
      </p>
      <button onClick={() => up(carditem)}>+</button>
      <button onClick={() => down(carditem)}>-</button>
      <button className="del-btn" onClick={() => del(carditem)}>
        Remove
      </button>
    </div>
  );
}
