import React, { useState, useMemo } from "react";
import Card from "./Card";
import Header from "./Header";
import ProductList from "./ProductList";
import { products } from "../products";
const filterProduct = (category) => {
  if (category === "All") {
    return products;
  } else {
    return products.filter((elem) => elem.category === category);
  }
};
export default function MainApp() {
  const [filterText, setfilterText] = useState("All");
  const [card, setcard] = useState([]);
const [sorting,setSorting]=useState("")
  let prods = useMemo(() => filterProduct(filterText), [filterText]);
  const sortProd = (category) => {
    if (category === "up") {
        setSorting("up")
      return prods.sort((a, b) => a.price - b.price);
    } else if (category === "down") {
        setSorting("down")
      return prods.sort((a, b) => b.price - a.price);
    }
  };
  const addToCard = (item) => {
    if (!card.includes(item)) {
      item.quantity = 1;
      card.push(item);
    } else {
      item.quantity++;
    }
    setcard([...card]);
  };
  const totalPrice = (card) => {
    return card.reduce((sum, item) => sum + item.price * item.quantity, 0);
  };
  const total = useMemo(() => totalPrice(card), [card]);

  const itemQuantityUp = (item) => {
    item.quantity++;
    setcard([...card]);
  };
  const itemQuantityDown = (item) => {
    item.quantity--;
    if (item.quantity <= 0) {
      card.splice(card.indexOf(item), 1);
    }
    setcard([...card]);
  };
  const deleteItem = (item) => {
    card.splice(card.indexOf(item), 1);
    setcard([...card]);
  };
  const clearAllOrders = () => {
    setcard([]);
  };
  const changeFilterText = (text) => {
    setfilterText(text);
  };
  return (
    <div>
      <Header onfilter={changeFilterText} />
      <div className="flex">
        <ProductList
          productList={prods}
          addToCard={addToCard}
          onSort={sortProd}
        />
        <Card
          cards={card}
          total={total}
          up={itemQuantityUp}
          down={itemQuantityDown}
          del={deleteItem}
          clearAllOrders={clearAllOrders}
        />
      </div>
    </div>
  );
}
