import React from "react";

export default function Categories({onfilter}) {
  const categorie = ["All", "Fruits", "Pizza", "Burger", "Coffee", "Drink"];
  return <>
<ul className="horizontal">
    {categorie.map((cat,i)=>(
        <li key={i} onClick={()=>onfilter(cat)}>{cat}</li>
    ))}
</ul>
  </>;
}
