import React from "react";
import ProductItem from "./ProductItem";

export default function ProductList({ productList, addToCard, onSort }) {
  return (
    <div className="vertical">
      <div className="buttons-sort">
        <button
          onClick={() => {
            onSort("down");
          }}
        >
          Sort down price
        </button>
        <button
          onClick={() => {
            onSort("up");
          }}
        >
          Sort up price
        </button>
      </div>

      <div className="flex">
        {productList.map((product, i) => (
          <ProductItem key={i} product={product} addToCard={addToCard} />
        ))}
      </div>
    </div>
  );
}
