import React from "react";

export default function ProductItem({ product, addToCard }) {
  return (
    <div className="product-item">
      <img src={require("../" + product.photo)} alt="product" />
      <p>{product.name}</p>
      <p>{product.price}AMD</p>
      <button onClick={() => addToCard(product)}>Order</button>
    </div>
  );
}
