import React from "react";
import CardItem from "./CardItem";

export default function Card({ cards, total, up, down, del, clearAllOrders }) {
  return (
    <div>
      {total > 0 && <p className="total">Total-{total}AMD</p>}
      {cards.map((elem, i) => (
        <CardItem key={i} carditem={elem} up={up} down={down} del={del} />
      ))}
      {cards.length > 0 && (
        <button className="del-btn" onClick={clearAllOrders}>
          Clear All
        </button>
      )}
    </div>
  );
}
